
'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { BookOpenText, Users, MessageSquareText, Newspaper, Languages, Tv, ChevronLeft, ChevronRight } from 'lucide-react';

const languageHighlights = [
  { name: 'Inglés', icon: Languages },
  { name: 'Francés', icon: Languages },
  { name: 'Alemán', icon: Languages },
  { name: 'Portugués', icon: Languages },
];

const modalityHighlights = [
  { name: 'Presencial', icon: Users },
  { name: 'Semi-intensivo', icon: Tv },
  { name: 'Intensivo', icon: Tv },
];

const recentNews = [
  { id: '1', title: 'Convocatoria Cursos de Extensión primer semestre 2025', date: '2024-11-10', image: 'https://placehold.co/300x200/00723F/FFFFFF', aiHint: 'university announcement students' },
  { id: '2', title: 'Nuevo laboratorio de idiomas inaugurado', date: '2024-10-25', image: 'https://placehold.co/300x200/FFD100/333333', aiHint: 'language lab computers' },
  { id: '3', title: 'Experiencias de intercambio estudiantil', date: '2024-10-15', image: 'https://placehold.co/300x200/005DAA/FFFFFF', aiHint: 'students traveling group' },
];

const heroImages = [
  { src: 'https://placehold.co/1920x700/00723F/FFFFFF?text=Centro+de+Idiomas', alt: 'Centro de Idiomas UNILLANOS', aiHint: 'university building languages', title: 'Transforma tu futuro aprendiendo idiomas', description: 'En el Centro de Idiomas de la Universidad de los Llanos te ofrecemos programas de alta calidad para alcanzar tus metas personales y profesionales.' },
  { src: 'https://placehold.co/1920x700/FFD100/333333?text=Estudiantes+Felices', alt: 'Estudiantes aprendiendo en UNILLANOS', aiHint: 'students classroom learning', title: 'Nuevas Culturas, Nuevas Oportunidades', description: 'Domina un nuevo idioma y expande tus horizontes académicos y laborales con nuestros expertos.' },
  { src: 'https://placehold.co/1920x700/005DAA/FFFFFF?text=Conexión+Global', alt: 'Conexión Global a través de los Idiomas', aiHint: 'global culture travel', title: 'Comunícate con el Mundo', description: 'Aprende inglés, francés, alemán o portugués y conéctate con personas de todo el planeta.' },
];


export default function HomePage() {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentImageIndex((prevIndex) => (prevIndex + 1) % heroImages.length);
    }, 7000); // Change image every 7 seconds
    return () => clearInterval(timer);
  }, []);

  const goToPrevious = () => {
    setCurrentImageIndex((prevIndex) => (prevIndex - 1 + heroImages.length) % heroImages.length);
  };

  const goToNext = () => {
    setCurrentImageIndex((prevIndex) => (prevIndex + 1) % heroImages.length);
  };

  return (
    <>
      {/* Hero Section with Carousel */}
      <section id="hero" className="relative text-white py-20 md:py-32 flex items-center justify-center text-center overflow-hidden h-[550px] md:h-[calc(100vh-4rem)] min-h-[500px]">
        {heroImages.map((image, index) => (
          <div
            key={image.src}
            className={`absolute inset-0 w-full h-full transition-all duration-1000 ease-in-out ${
              index === currentImageIndex ? 'opacity-100 transform scale-100' : 'opacity-0 transform scale-105'
            } ${
              // Logic for sliding: if the transitioning image is "before" current, it slides left. If "after", it slides right.
              // This is a simplified example. For more robust sliding, current index vs target index needs consideration.
              (index < currentImageIndex && currentImageIndex !== 0) || (currentImageIndex === 0 && index === heroImages.length -1 && heroImages.length > 1) ? 
              (index === currentImageIndex ? '' : '-translate-x-full') : 
              (index > currentImageIndex && currentImageIndex !== heroImages.length -1) || (currentImageIndex === heroImages.length -1 && index === 0 && heroImages.length > 1) ? 
              (index === currentImageIndex ? '' : 'translate-x-full') : ''
            }`}
          >
            <Image
              src={image.src}
              alt={image.alt}
              layout="fill"
              objectFit="cover"
              quality={85}
              className="absolute inset-0 z-0"
              data-ai-hint={image.aiHint}
              priority={index === 0}
            />
            <div className="absolute inset-0 bg-black/50 z-0"></div> {/* Dark overlay for text contrast */}
          </div>
        ))}

        <div className="relative z-10 container mx-auto px-4 md:px-6">
          <BookOpenText className="mx-auto h-16 w-16 mb-6 text-secondary" />
          <div key={currentImageIndex} className="animate-in fade-in-0 slide-in-from-bottom-10 duration-700">
            <h1 className="font-headline text-4xl md:text-6xl font-bold mb-6">
              {heroImages[currentImageIndex].title}
            </h1>
            <p className="text-lg md:text-xl mb-8 max-w-3xl mx-auto">
              {heroImages[currentImageIndex].description}
            </p>
          </div>
          <Button asChild size="lg" className="bg-secondary hover:bg-secondary/90 text-secondary-foreground">
            <Link href="/oferta-academica">Descubre nuestros cursos</Link>
          </Button>
        </div>
        
        {/* Carousel Controls */}
        <button 
          onClick={goToPrevious} 
          className="absolute top-1/2 left-4 transform -translate-y-1/2 z-20 p-2 bg-black/30 hover:bg-black/50 rounded-full text-white transition-colors"
          aria-label="Imagen anterior"
        >
          <ChevronLeft size={32} />
        </button>
        <button 
          onClick={goToNext} 
          className="absolute top-1/2 right-4 transform -translate-y-1/2 z-20 p-2 bg-black/30 hover:bg-black/50 rounded-full text-white transition-colors"
          aria-label="Siguiente imagen"
        >
          <ChevronRight size={32} />
        </button>

        {/* Dots Indicator */}
        <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 z-20 flex space-x-2">
          {heroImages.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentImageIndex(index)}
              className={`h-3 w-3 rounded-full transition-all ${index === currentImageIndex ? 'bg-secondary scale-125' : 'bg-white/50 hover:bg-white/75'}`}
              aria-label={`Ir a imagen ${index + 1}`}
            ></button>
          ))}
        </div>
      </section>

      {/* Quick Summary Sections */}
      <section className="py-16 md:py-24 bg-background">
        <div className="container mx-auto px-4 md:px-6">
          <div className="grid md:grid-cols-2 gap-12 items-center">
            <div>
              <h2 className="font-headline text-3xl font-bold text-primary mb-4">Idiomas Disponibles</h2>
              <p className="text-lg text-foreground/80 mb-6">
                Ofrecemos una variedad de idiomas para que elijas el que más se adapte a tus intereses y necesidades.
              </p>
              <div className="grid grid-cols-2 gap-4">
                {languageHighlights.map(lang => (
                  <div key={lang.name} className="flex items-center gap-3 p-3 bg-card rounded-lg shadow hover:shadow-md transition-shadow">
                    <lang.icon className="h-6 w-6 text-primary" />
                    <span className="font-medium">{lang.name}</span>
                  </div>
                ))}
              </div>
            </div>
            <div>
              <h2 className="font-headline text-3xl font-bold text-primary mb-4">Modalidades de Estudio</h2>
              <p className="text-lg text-foreground/80 mb-6">
                Nuestros cursos se adaptan a tu ritmo de vida con diversas modalidades de aprendizaje.
              </p>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                {modalityHighlights.map(mod => (
                  <div key={mod.name} className="flex items-center gap-3 p-3 bg-card rounded-lg shadow hover:shadow-md transition-shadow">
                    <mod.icon className="h-6 w-6 text-primary" />
                    <span className="font-medium">{mod.name}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </section>
      
      {/* Recent News Section Placeholder */}
      <section id="recent-news" className="py-16 md:py-24 bg-card">
        <div className="container mx-auto px-4 md:px-6">
          <div className="text-center mb-12">
            <Newspaper className="mx-auto h-12 w-12 text-primary mb-4" />
            <h2 className="font-headline text-3xl md:text-4xl font-bold text-primary mb-3">
              Noticias Recientes
            </h2>
            <p className="text-lg text-foreground/80 max-w-2xl mx-auto">
              Mantente al día con las últimas novedades y eventos de nuestro Centro de Idiomas.
            </p>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {recentNews.map(news => (
              <Card key={news.id} className="overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300">
                <CardHeader className="p-0">
                  <Image src={news.image} alt={news.title} width={400} height={200} className="w-full h-48 object-cover" data-ai-hint={news.aiHint} />
                </CardHeader>
                <CardContent className="p-6">
                  <p className="text-sm text-muted-foreground mb-1">{news.date}</p>
                  <CardTitle className="font-headline text-xl mb-2 text-primary hover:text-primary/80">
                    <Link href={`/noticias/${news.id}`}>{news.title}</Link>
                  </CardTitle>
                  <Button variant="link" asChild className="p-0 h-auto text-secondary hover:text-secondary/80">
                    <Link href={`/noticias/${news.id}`}>Leer más &rarr;</Link>
                  </Button>
                </CardContent>
              </Card>
            ))}
          </div>
          <div className="text-center mt-12">
            <Button asChild variant="outline">
              <Link href="/noticias">Ver todas las noticias</Link>
            </Button>
          </div>
        </div>
      </section>
    </>
  );
}
